import React from 'react'
import { connect } from "react-redux";
import {
  followSuccess,
  unfollowSuccess,
  toggleFollowingInProgress, getUsers, follow, unfollow
} from "../../Redux/users-reduser";
import Users from "./Users";
import { compose } from "redux";
import {
  getUsersSelector, getPageSizeSelector,
  getTotalUsersCountSelector, getCurrentPageSelector,
  getFetchingStatusSelector, getFollowingInProgressSelector, getPagePortionSizeSelector
} from '../../Redux/users-selectors'

class UsersContainer extends React.Component {

  componentDidMount() {
    const currentPage = this.props.currentPage
    const pageSize = this.props.pageSize
    this.props.getUsers(currentPage, pageSize)
  }

  onPageChanged = (pageNumber) => {
    const pageSize = this.props.pageSize
    this.props.getUsers(pageNumber, pageSize)
  };

  render() {
    return <>
      <Users totalItemsCount={this.props.totalItemsCount}
             pageSize={this.props.pageSize}
             portionSize={this.props.portionSize}
        currentPage={this.props.currentPage}
        onPageChanged={this.onPageChanged}
        users={this.props.users}
        isFetching={this.props.isFetching}
        followingInProgress={this.props.followingInProgress}
        toggleFollowingInProgress={this.props.toggleFollowingInProgress}
        follow={this.props.follow}
        unfollow={this.props.unfollow}
      />
    </>
  }
}


const mapStateToProps = (state) => {
  return {
    users: getUsersSelector(state),
    pageSize: getPageSizeSelector(state),
    totalItemsCount: getTotalUsersCountSelector(state),
    currentPage: getCurrentPageSelector(state),
    isFetching: getFetchingStatusSelector(state),
    followingInProgress: getFollowingInProgressSelector(state),
    portionSize: getPagePortionSizeSelector(state)
  }
};

export default compose(
  connect(mapStateToProps,
    {
      followSuccess, unfollowSuccess, toggleFollowingInProgress,
      getUsers, follow, unfollow
    }),
)(UsersContainer);