import { createSelector } from "reselect"

export const returnUsers = (state) => {
  return state.usersPage.users
}

export const getFetchingStatusSelector = (state) => {
  return state.usersPage.isFetching
}

export const getUsersSelector = createSelector(returnUsers, (users) => {
  return users.filter((u) => true)

})
export const getPagePortionSizeSelector = (state) => {
  return state.usersPage.portionSize
}
export const getPageSizeSelector = (state) => {
  return state.usersPage.pageSize
}
export const getTotalUsersCountSelector = (state) => {
  return state.usersPage.totalUsersCount
}
export const getCurrentPageSelector = (state) => {
  return state.usersPage.currentPage
}

export const getFollowingInProgressSelector = (state) => {
  return state.usersPage.followingInProgress
}