import React from 'react'
import s from './Preloader.module.css'

const Preloader = () => {
  return (
      <div className={s.preloader}>
        <div className={s.ldsCss + ' ' + s.ngScope}>
          <div  className={s.ldsDualRing}>
            <div />
            <div>
              <div />
            </div>
          </div>
        </div>
      </div>
  )
};

export default Preloader