const SEND_MESSAGE = 'SEND-MESSAGE';

export const sendMessageCreator = (messageText) =>
    ({type: SEND_MESSAGE, messageText});

let initialState = {
  dialogs: [
    {id: 1, name: 'Михайло'},
    {id: 2, name: 'Антонио'},
    {id: 3, name: 'Филипино'},
    {id: 4, name: 'Святославио'},
    {id: 5, name: 'Александрио'}
  ],
  messages: [
    {id: 1, message: 'Hello'},
    {id: 2, message: 'Niger'},
    {id: 3, message: 'Bitch'}
  ],
  newMessageText: ''
};

const dialogsReducer = (state = initialState, action) => {

  switch (action.type) {
    case SEND_MESSAGE:
      if (action.messageText){
        return {
          ...state,
          messages: [
            ...state.messages,
            {id: 5, message: action.messageText}
          ]
        };
      }
      return {...state}

      /* falls through */
    default:
      return state;
  }
};

export default dialogsReducer