import React from 'react'
import { reduxForm } from "redux-form";
import { Input } from "../common/FormControls/FormsControls";
import { maxLengthCreator, required } from "../../utils/validators/validators";
import { connect } from "react-redux";
import { login } from "../../Redux/auth-reduser";
import { Redirect } from "react-router-dom";
import { createField } from "../common/FormControls/FormsControls"
import s from '../common/FormControls/FormsControls.module.css'

const Login = (props) => {
  const onSubmit = (formData) => {
    props.login(formData.email, formData.password, formData.rememberMe)
  };
  if (props.isAuth) {
    return <Redirect to={'/profile'} />
  } else {
    return <div>
      <h1>Login</h1>
      <LoginReduxForm onSubmit={onSubmit} />
    </div>
  }
};


const maxlength20 = maxLengthCreator(20)


const LoginForm = ({ handleSubmit, error }) => {
  return <form onSubmit={handleSubmit}>
    {createField('Email', 'email', [required], Input)}
    {createField('password', 'password', [required, maxlength20], Input, { type: "password" })}
    {createField('rememberMe', 'rememberMe', [], Input, { type: "checkbox" }, "Remember me")}

    {
      error &&
      <div className={s.formSummaryError}>
        {error}
      </div>
    }

    <button>Login</button>
  </form>
};

const LoginReduxForm = reduxForm({ form: 'login' })(LoginForm);

const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth
});

export default connect(mapStateToProps, { login })(Login)