import React, {useState} from 'react'
import s from './Paginator.module.css'

const Paginator = ({totalItemsCount, currentPage, onPageChanged, portionSize, pageSize}) => {

  let pagesCount = Math.ceil(totalItemsCount / pageSize);
  const pages = [];
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i)
  }
  const portionCount = Math.ceil(pagesCount / portionSize);
  const [portionNumber, setPortionNumber] = useState(1);
  const leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
  const rightPortionPageNumber = (portionNumber) * portionSize;

  return (
      <div className={s.pagination}>
          <button className={s.button} disabled={portionNumber <= 1} onClick={() => setPortionNumber(portionNumber - 1)}>Prev</button>
        <ul>
          {pages
              .filter(p => p >= leftPortionPageNumber && p<=rightPortionPageNumber)
              .map(p => {
            return <li
                key={p}
                className={currentPage === p ? s.selectedPage : ''}
                onClick={() => onPageChanged(p)}
            >{p}</li>
          })}
        </ul>
          <button className={s.button} disabled={portionNumber >= portionCount} onClick={() => setPortionNumber(portionNumber + 1)}>Next</button>
      </div>
  )
};
export default Paginator